@tailwind base;
@tailwind components;
@tailwind utilities;


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.slider-container {
  width: 80%;
  margin: 50px auto;
  text-align: center;
}

/* Style the slider track */
.slider {
  -webkit-appearance: none; /* Remove default WebKit appearance */
  appearance: none;
  width: 100%;
  height: 15px;
  background: var(--SliderBackgroundColor);
  border-radius: 5px;
  outline: none;
  transition: background 0.3s;
}

/* Webkit browsers (Chrome, Safari) */
.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  background-color: var(--SliderColor) !important;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: var(--thumb-color, rgba(238, 79, 5, 0.808)); /* Use custom thumb color */
  cursor: pointer;
  transition: background 0.3s;
}

/* Firefox */
.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: var(--thumb-color, blue); /* Use custom thumb color */
  cursor: pointer;
  transition: background 0.3s;
}

/* Internet Explorer */
.slider::-ms-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: var(--thumb-color, blue); /* Use custom thumb color */
  cursor: pointer;
  transition: background 0.3s;
}